@-webkit-keyframes spin {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

@keyframes spin {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.dice-container {
  margin-top: 50px;
  text-align: center;
}

.spin-die {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: .125s;
          animation-duration: .125s;
  -webkit-animation-iteration-count: 45;
          animation-iteration-count: 45;
}

#dice-body {
  width: 100px;
  height: 100px;
  background-color: #000;
  border: 1px solid #50BFE6;
  border-radius: 10px;
  box-shadow: 0px 2px 6px #50BFE6;
  display: inline-grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  grid-template-areas:
    "tl tc tr"
    "cl cc cr"
    "bl bc br";
  justify-items: center;
  align-items: center;
}

@media only screen and (max-width: 400px) {
  #dice-body {
    width: 70px;
    height: 70px;
  }
}


.dot {
  width: 16px;
  height: 16px;
  padding: 5px;
  border-radius: 100%;
  background-color: #50BFE6;
  display: none;
}

@media only screen and (max-width: 460px) {
  .dot {
    width: 8px;
    height: 8px;
  }
}

.show {
  display: block;
}

#tl { grid-area: tl; }
#tc { grid-area: tc; }
#tr { grid-area: tr; }
#cl { grid-area: cl; }
#cc { grid-area: cc; }
#cr { grid-area: cr; }
#bl { grid-area: bl; }
#bc { grid-area: bc; }
#br { grid-area: br; }

.roll-btn {
  width: 70px;
  height: 70px;
  border-radius: 100%;
  font-size: 30px;
  padding: 10px;
  box-shadow: 0 2px 6px #CCFF00;
  background-color: #000;
  color: #CCFF00;
  outline: none;
  border: none;
}

.roll-btn:active {
  background-color: #000;
  color: #FF9933;
  box-shadow: 0 2px 6px #FF9933; 
  border: 1px solid #FF9933;
}


.cmenu {
  text-align: center;
}

.relative-menu {
  position: relative;
  overflow: hidden;
  width: 300px;
  height: 400px;
  display: inline-block;
}

.fa-play {
  font-size: 30px;
  padding-left: 5px;
}

.radmenu > ul {
  list-style-type:none;
}

@-webkit-keyframes eye-focus {
  0% {
    -webkit-filter: blur(3px);
  }
  100% {
    -webkit-filter: blur(10px);
  }
}

@keyframes eye-focus {
  0% {
    -webkit-filter: blur(3px);
  }
  100% {
    -webkit-filter: blur(10px);
  }
}

@-webkit-keyframes focus-shift {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}

@keyframes focus-shift {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}

.radmenu {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.radmenu > button {
  top: calc(50% - 40px);
  left: calc(50% - 40px);
}

.radmenu > button.show {
  display: flex !important;
}

.radmenu li {
  display: list-item;
  transform: translate3d(0, 0, 0);
  transition: all 1s ease;
}

.radmenu button {
  position: absolute;
  width: 80px;
  height: 80px;
  background: #000000;
  text-align: center;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  display: none;
  text-decoration: none;
  color: 	#CCFF00;
  transition: all 1s ease;
  box-shadow: 0 0 15px #CCFF00;
  font-family: "segoe ui";
  font-weight: 400;
  font-size: 16px;
  outline: none;
  border: none;
}

.radmenu .selected {
  background: rgba(51, 51, 51, 0.9);
  display: flex;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
  color: #f1f1f1;
  box-shadow: 0 0 10px #f1f1f1;
}

.radmenu .selected + ul > li:nth-child(1) {
  transform: rotate(30deg) translateX(60px);
}

.radmenu .selected + ul > li:nth-child(1) > button {
  transform: rotate(-30deg);
}

.radmenu .selected + ul > li:nth-child(2) {
  transform: rotate(105deg) translateX(60px);
}

.radmenu .selected + ul > li:nth-child(2) > button {
  transform: rotate(-105deg);
}

.radmenu .selected + ul > li:nth-child(3) {
  transform: rotate(248deg) translateX(60px);
}

.radmenu .selected + ul > li:nth-child(3) > button {
  transform: rotate(-248deg);
}

.radmenu .selected + ul > li > button {
  display: flex;
}

